var token=""
import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	}, 
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			hasLogin:false,
			cutId:"",
			ids:[]
		};
	},
	onLoad(){
		
	},
	onShow() {
		token = uni.getStorageSync('token')
		console.log(this.$userKey)
		var userInfo = uni.getStorageSync(this.$userKey)
		console.log(userInfo)
		this.cartList=[]
		this.allChecked= false
		this.total = 0
		if(userInfo.nickname.length>0)
		{
		this.hasLogin=true
		this.loadData();
		}else
		{	
		this.hasLogin=false
		}
			
	},
	watch:{
		//显示空白页
		cartList(e){
			let empty = e.length === 0 ? true: false;
			if(this.empty !== empty){
				this.empty = empty;
			}
		}
	},
	computed:{
		...mapState([''])
	},
	methods: {
		//请求数据
		
		async loadData(){
			var _this= this
			uni.request({
			    url:this.$url+'/car/all' ,//服务器端地址
			    data: {
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.cartList = res.data.data
			        }
			    }
			   
			});
		},
		add(e)
		{
			console.log(e.id)
			this.cutId = e.id
			this.addOrJ("/car/num/add/"+e.id,0)
		},
		jian(e)
		{
			this.cutId = e.id
			this.addOrJ("/car/num/decrease/"+e.id,1)
			
		},
		//增加或者减少数量
		addOrJ(method,type)
		{
			console.log(token)
			var _this = this
			uni.request({
			    url:this.$url+method ,//服务器端地址
			    data: {
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.addNum(type)
			        }else
					{
						_this.$api.msg(res.data.message)
					}
			    }
			   
			});
			},
		//增加或者减少数量（更新视图）
		addNum(mdType)
		{
			for(var i=0;i<this.cartList.length;i++)
			{
				if(this.cartList[i].id==this.cutId)
				{
					if(mdType==0)
					{
						this.cartList[i].number=this.cartList[i].number+1
					}else
					{
						this.cartList[i].number=this.cartList[i].number-1
					}
					
					
				}
			}
		},
		//删除事件
		deleteM()
		{
			var _this = this
			uni.showModal({
				title:"提示",
				content:"确定要删除吗？",
				confirmText:"确定",
				confirmColor:"#333233",
				cancelText:"取消",
				cancelColor:"#909399",
				success() {
					_this.deleteGoods()
				}
			})
		},
		//获取要删除的IDs
		deleteIds()
		{
			this.ids=[]
			for(var i=0;i<this.cartList.length;i++)
			{
				if(this.cartList[i].checked)
				{
					 this.ids.push(this.cartList[i].id)
					//this.cartList.splice(i, 1)
				}
			}
		},
		//删除商品
		deleteGoods()
		{
			this.deleteIds()
			console.log(this.ids)
			if(this.ids.length==0)
			{
				uni.showToast({
					title:"请至少选择一个商品",
					icon:"none"
				})
				return
			}
			var _this = this
			uni.request({
			    url:this.$url+"/car/delete" ,//服务器端地址
			    data: {
					ids:this.ids
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
					_this.$api.msg(res.data.message)
			        if (res.data.code == 200) {
						_this.cartList=[]
						_this.loadData()
			        }
			    }
			   
			});
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index){
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index){
			let list = this.cartList;
			let row = list[index];
			let id = row.id;

			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder(){
			var datas = this.initData()
			if(datas.length===0)
			{
				this.$api.msg("请至少选择一个商品")
				return
			}
			 
			uni.navigateTo({
				url: `/pages/order/createOrder?orders=`+encodeURIComponent(JSON.stringify(datas))+"&price="+this.total
			})
			
		},
		
		initData()
		{	
			var list=[]
			for(var i=0;i<this.cartList.length;i++)
			{
				
				if(this.cartList[i].checked)
				{
					list.push(this.cartList[i])	
				}
				
			}
			return list
		}
	}
}
